import React from "react";
import { useQuery } from "react-query3";
import queryClient, { useMutation } from "components/utils/queryClient";
import { Button, PageHeader, Space, Tooltip, Select, Modal as AntModal, Form, Input, Switch, notification, Popconfirm, message, Typography } from "antd";
import { CustomColumns, Authentication, AuthenticationMethodType } from "../../types";
import Table from "../ui/Table";
import { ExclamationCircleOutlined, EditFilled } from "@ant-design/icons";
import EditorButton from "components/ui/EditorButton";
import useNormalizeData from "../utils/normalizedRecord";
import RoleGuard from "components/standalone/role-guard";
import DocsButton from "components/docsButton";
const DeleteButton = React.lazy(() => import("components/ui/DeleteButton"));

const { Option } = Select;


const Saml2Options = (props) => {
  const [open, setOpen] = React.useState(false);
  const [saving, setIsSaving] = React.useState(false);
  const [form] = Form.useForm();
  const { mutateAsync } = useMutation<any>();
  const normalizedRecord = useNormalizeData();

  const onOk = React.useCallback(
    async () => {
      let values = await form.validateFields();

      setIsSaving(true);

      await mutateAsync(
        { key: `/settings/authentication/${props.resource.id}`, action: "update", ...normalizedRecord({ ...props.resource, ...values }) },
        {
          onError: (error) => {
            setOpen(false);
            setIsSaving(false);
            notification.error({
              message: "Error",
              description: error.response?.data,
              icon: <ExclamationCircleOutlined style={{ color: 'red' }} />,
            })
          },
          onSuccess: (data, variables) => {
            setIsSaving(false);
            setOpen(false);
            queryClient.refetchQueries("/settings/authentication");
          },
        }
      );
    },
    [form, mutateAsync, normalizedRecord, props.resource]
  );


  const handleOk = () => {
    onOk();
  }
  const handleCancel = () => {
    setOpen(false);
  }

  return (
    <>
      <RoleGuard requiredRoles={["Administrator", "Operator"]} allowedWithOneWayGitSync={false}>
        <Tooltip title="Edit">
          <Button onClick={() => setOpen(true)} icon={<EditFilled />} type="text"></Button>
        </Tooltip>
      </RoleGuard>

      <AntModal
        title="SAML2 Options"
        visible={open}
        onCancel={() => setOpen(false)}
        footer={[
          <DocsButton url="config/security/saml2" style={{ float: 'left' }} />,
          <Button key="back" onClick={handleCancel}>
            Cancel
          </Button>,
          <Button key="submit" type="primary" onClick={handleOk} loading={saving}>
            Save
          </Button>,
        ]}
      >
        <Form
          size="small"
          form={form}
          layout="vertical"
          initialValues={{ ...props.resource }}
        >
          <Form.Item
            name="entityId"
            label="Entity ID"
            tooltip="The service provider entity ID."
            style={{ marginBottom: 0 }}
            rules={[{ required: true, message: 'The entity ID is required' }]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="identityProviderEntityId"
            label="Identity Provider Entity ID"
            tooltip="The identity provider entity ID. This will be used as the metadata address if none is specified."
            rules={[{ required: true, message: 'The identity provider entity ID is required' }]}
            style={{ marginBottom: 0 }}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="metadataAddress"
            label="Metadata Address"
            tooltip="The metadata address if it is different than the identity provider ID."
            style={{ marginBottom: 0 }}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="callbackPath"
            label="Return URL"
            tooltip="The return URL after authentication has been completed."
            style={{ marginBottom: 0 }}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="singleSignOnServiceUrl"
            label="Single Sign-On Service URL"
            tooltip="The URL to redirect to to perform the single sign-on request."
            style={{ marginBottom: 0 }}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="signingKey"
            label="Signing Key"
            tooltip="The path to a signing key (.cert) file."
            style={{ marginBottom: 0 }}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="serviceCertificate"
            label="Service Certificate"
            tooltip="The certificate responsible for signing requests. This can be a path to a file or the distinguished named of a certificate."
            style={{ marginBottom: 0 }}
          >
            <Input />
          </Form.Item>
        </Form>
      </AntModal>
    </>
  )
}

