export default function Authentications() {
  const { mutateAsync } = useMutation<any>();
  const [addingAuth, setIsAddingAuth] = React.useState(false);


  const { data: authentications, isLoading, refetch } = useQuery<Authentication[]>("/settings/authentication", {
    refetchOnWindowFocus: false,
  });

  const toggleEnabled = React.useCallback(
    async (record: Authentication) => {

      if (authentications.filter(m => !m.disabled).length === 1 && !record.disabled) {
        message.error("You must have at least one enabled authentication method.");
        return;
      }

      await mutateAsync(
        { key: `/settings/authentication/${record.id}`, action: "update", ...record, disabled: !record.disabled },
        {
          onSuccess: (data, variables) => {
            refetch();
          },
        }
      );
    },
    [mutateAsync, refetch, authentications]
  );

  const addAuthenticationMethod = React.useCallback(
    async (type: AuthenticationMethodType) => {
      setIsAddingAuth(true);

      await mutateAsync(
        { key: `/settings/authentication`, action: "create", type, disabled: true },
        {
          onError: (error) => {
            setIsAddingAuth(false);
            notification.error({
              message: "Error",
              description: error.response.data,
              icon: <ExclamationCircleOutlined style={{ color: 'red' }} />,
            })
          },
          onSuccess: (data, variables) => {
            setIsAddingAuth(false);
            refetch();
          },
        }
      );
    },
    [mutateAsync, refetch]
  );


  const availableMethods = [
    AuthenticationMethodType.Form,
    AuthenticationMethodType.OpenIDConnect,
    AuthenticationMethodType.Saml2,
    AuthenticationMethodType.Windows,
    AuthenticationMethodType.WSFederation
  ].filter(x => authentications?.map(m => m.type).indexOf(x) === -1);

  const convertToName = (authenticationType: AuthenticationMethodType) => {
    switch (authenticationType) {
      case AuthenticationMethodType.Form:
        return "Form";
      case AuthenticationMethodType.Windows:
        return "Windows";
      case AuthenticationMethodType.OpenIDConnect:
        return "OpenID Connect";
      case AuthenticationMethodType.WSFederation:
        return "WS-Federation";
      case AuthenticationMethodType.Saml2:
        return "SAML2";
    }
  }

  const columns: CustomColumns<Authentication>[] = [
    {
      key: "type",
      title: "Type",
      dataIndex: "type",
      editable: false,
      sorter: (a, b) => convertToName(a.type).localeCompare(convertToName(b.type)),
      sortDirections: ['descend'],
      render: (text, record) => <Space>
        {convertToName(record.type)}
      </Space>
    },
    {
      key: "source",
      title: "Source",
      dataIndex: "source",
      editable: false
    },
    {
      key: "enabled",
      title: "Enabled",
      dataIndex: "enabled",
      editable: false,
      render: (text, record) => {
        return (<Popconfirm disabled={record.source === "appsettings.json"} okText="Yes" cancelText="No" onConfirm={() => toggleEnabled(record)} title={`Are you sure you want to ${record.disabled ? "enabled" : "disable"} this authentication method? You may lose access to the admin console if not configured properly.`}>
          <Switch disabled={record.source === "appsettings.json"} checked={!record.disabled} />
        </Popconfirm>
        )
      }
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      editable: false,
      render: (text, record) => {
        if (record.source === 'appsettings.json') return <></>;

        if (record.type === AuthenticationMethodType.Form) {
          return (
            <Space>
              <EditorButton to={`${record?.id}`} allowedWithOneWayGitSync={false} />
            </Space>
          );
        }

        if (record.type === AuthenticationMethodType.OpenIDConnect) {
          return (
            <Space>
              <OpenIdConnectOptions resource={record} allowedWithOneWayGitSync={false} />
              <DeleteButton resource={record} refetch={["/settings/authentication"]} allowedWithOneWayGitSync={false} />
            </Space>
          )
        }

        if (record.type === AuthenticationMethodType.WSFederation) {
          return (
            <Space>
              <WsFedOptions resource={record} allowedWithOneWayGitSync={false} />
              <DeleteButton resource={record} refetch={["/settings/authentication"]} allowedWithOneWayGitSync={false} />
            </Space>
          )
        }


        if (record.type === AuthenticationMethodType.Saml2) {
          return (
            <Space>
              <Saml2Options resource={record} allowedWithOneWayGitSync={false} />
              <DeleteButton resource={record} refetch={["/settings/authentication"]} allowedWithOneWayGitSync={false} />
            </Space>
          )
        }

        return (
          <Space>
            <DeleteButton resource={record} refetch={["/settings/authentication"]} allowedWithOneWayGitSync={false} />
          </Space>)
      },
    },
  ];

  return (
    <PageHeader title="Authentication" subTitle="Authentication methods for the platform." extra={[
      availableMethods.length > 0 && (
        <RoleGuard requiredRoles={["Administrator", "Operator"]} allowedWithOneWayGitSync={false}>
          <Select style={{ width: 250 }} placeholder={<Typography>Add Authentication Method</Typography>} onChange={addAuthenticationMethod} loading={addingAuth} disabled={addingAuth}>
            {availableMethods.map(x => <Option key={x} value={x}>{convertToName(x)}</Option>)}
          </Select>
        </RoleGuard>),
      <DocsButton url="config/security#forms-authentication" />
    ]}>
      <Table
        title="Authentication"
        data={authentications}
        columns={columns}
        loading={isLoading}
        queryFn={refetch}
      />
    </PageHeader>
  );
}